import React from "react";
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { createTheme, ThemeProvider } from "@mui/material/styles";
import 'dayjs/locale/ko';
import dayjs, {Dayjs} from "dayjs";
import { Controller } from "react-hook-form";

// control ,
const theme = createTheme({
    components: {
        MuiFormLabel: {
            styleOverrides: {
                root : {
                    fontSize : '13px',
                }
            },
        },
        MuiInputBase : {
            styleOverrides : {
                root : {
                    width : '320px',
                    height : '56px',
                    fontSize : '13px'
                }
            }
        }
    },
});

function InputDatePicker({ control, strDate, setStrDate } : any ) {
    return (
        <div className='flex w-full'>
            <div className='mr-auto'>
                <Controller
                    rules={{ required: true}}
                    control={control}
                    name='strDate'
                    // render , field    .
                    // field value onBlur   
                    // render onChange , onChange  
                    //   .
                    render={({ field: { onChange, ref } }) => (
                        // antd datepicker e.target.value
                        // moment   ,
                        // "2021-04-15"   ,  
                        // "dateString"    .
                        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ko">
                                    <ThemeProvider theme={theme}>
                                        <DesktopDatePicker
                                            slotProps={{
                                                toolbar: {
                                                    // Customize value display
                                                    //toolbarFormat: 'YYYY',
                                                    hidden: true,
                                                },
                                                actionBar: {
                                                    actions: ['cancel', 'accept'],
                                                },
                                                field: {
                                                    readOnly: true
                                                }
                                            }}
                                            onChange={(newDate : any) => {
                                                // @ts-ignore
                                                onChange(dayjs(newDate).format('YYYY-MM-DD'));
                                            }
                                            }
                                            inputRef={ref}
                                            label=""
                                            format={'YYYY-MM-DD'}
                                            shouldDisableDate={(day : any) => {
                                                return dayjs(dayjs(day as Dayjs).format(`YYYY-MM-DD`)).isBefore(
                                                    dayjs().subtract(1, 'day')
                                                );
                                            }}
                                        />
                                    </ThemeProvider>
                        </LocalizationProvider>
                    )}
                />
            </div>
        </div>
    );
}

export default InputDatePicker;
