import React from 'react';
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";
import ListSubheader from "@mui/material/ListSubheader";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import { Controller } from "react-hook-form";

function DiagSelect ({control} : any)  {
    return (
        <div>
            <Controller
                rules={{ required: true}}
                control={control}
                name='diagName'
                // render , field    .
                // field value onBlur   
                // render onChange , onChange  
                //   .
                render={({ field: { onChange, ref } }) => (
            <FormControl sx={{m: 1, minWidth: 320}}>
                <InputLabel htmlFor="grouped-select"></InputLabel>
                <Select defaultValue="" id="grouped-select" label="Grouping" onChange={onChange} inputRef={ref}>
                    <ListSubheader> </ListSubheader>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={'   '}>   </MenuItem>
                    <ListSubheader></ListSubheader>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={'  '}>  </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                    <MenuItem value={''}></MenuItem>
                    <MenuItem value={' '}> </MenuItem>
                </Select>
            </FormControl>
                    )}
            />
        </div>
    );
};

export default DiagSelect;
