import React, {useState, useRef} from 'react';
import SignatureCanvas from 'react-signature-canvas';
import styled from 'styled-components';
import {Buffer} from "buffer";
import axios from "axios";
import dayjs from "dayjs";

const CanvasWrapper = styled.div`
  position: relative;
  width: 350px;
  height: 200px;
`;

const CanvasPlaceholder = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: black;
`;
interface openType{
    signature : boolean;
    setSignature : React.Dispatch<React.SetStateAction<boolean>>;
    setSign : React.Dispatch<React.SetStateAction<any>>;
    name : string;
}
const Signature : React.FC<openType> = ({name, signature, setSignature,setSign}) => {
    const canvasRef = useRef<any>(null);
    const [isSigned, setIsSigned] = useState<boolean>(false);

    const save = async () => {
        try {
            const image = canvasRef.current.getTrimmedCanvas().toDataURL('image/png'); // image     , png jpg    
            const decodedURL = image.replace(/^data:image\/\w+;base64,/, '');
            const buf = Buffer.from(decodedURL, 'base64');
            const blob = new Blob([buf], { type: 'image/png' });
            const sign = new FormData();
            let today = dayjs().format('YYYY-MM-DD');
            const file = new File([blob], name + '__' + today.toString().replaceAll('-', '') + '.png', { type: 'image/png' });
            sign.append('sign',file);
            const response = await axios.post('/api/v1/dev/sign', sign, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            });

            if (response.status === 200) {
                console.log(response.data);
                setSign(response.data.signName);
            } else {
                console.error('Error uploading images:', response.statusText);
            }
        }catch(error) {
            console.log(error);
        }


        /*link.href = image; // a  href    
        link.download = 'sign_image.png'; //     
        link.click(); //  a    */
    };
    return (
        <div className='flex justify-center items-center  my-16 flex-col '>
            <CanvasWrapper className = 'border'>
                {!isSigned && (
                    <CanvasPlaceholder>
                          .
                    </CanvasPlaceholder>
                )}
                <SignatureCanvas
                    canvasProps={{width : '350px', height : '200px'}}
                    ref={canvasRef}
                    onBegin={() => {
                        setIsSigned(true);
                    }}
                />
            </CanvasWrapper>
            <div className='flex w-[350px] mt-4'>
                <button
                    type={'button'}
                    className='basis-1/2 mx-8 py-2 border'
                    onClick={(event : React.MouseEvent<HTMLButtonElement>) => {
                        canvasRef.current.clear(); // 
                        setIsSigned(false);
                        setSign('');
                    }}
                >
                    
                </button>
                <button
                    type={'button'}
                    className='basis-1/2 border mx-8'
                    disabled={!isSigned} //  disabled
                    onClick={() => save()}
                >
                    
                </button>
            </div>
        </div>
    );
};

export default Signature;
