import React, {useState, useEffect} from 'react';
import { SubmitHandler, useForm} from "react-hook-form";
import Signature from "./signature";
import axios from 'axios';
import dayjs from "dayjs";
import DiagSelect from "./diagSelect";
import BankSelect from "./bankSelect";
import InputDatePicker from "./inputDatePicker";
interface Inputs {
    image : any;
    birth : string,
    name : string,
    email : string,
    phone : string,
    diagName : string,
    strDate : string,
    account: number,
    bank : string
}

interface modalType {
    setMsg : React.Dispatch<React.SetStateAction<string>>;
    setIsOpen : React.Dispatch<React.SetStateAction<boolean>>;
}

const UserInfo : React.FC<modalType> = ( {setMsg,setIsOpen}) => {
    const Regex =
        {
            jumin : /\d{2}([0]\d|[1][0-2])([0][1-9]|[1-2]\d|[3][0-1])/g,
            number : /^(0|[1-9]\d*)(\.\d+)?$/
        };
    const [signature, setSignature] = useState(false);
    const {
        register,
        handleSubmit,
        watch,
        control,
        setFocus,
        formState: { errors },
    } = useForm<Inputs>({
        defaultValues: {
            image : ''
        },
        mode: 'onChange',
    });
    const name = watch("name");
    const [signFile, setSignFile] = useState('');
    const [imagePreview, setImagePreview] = useState("");
    const upload = watch("image");
    const onSubmit: SubmitHandler<Inputs> = async (data) => {
        try{
            console.log(data);
            if(signFile !== '' && signFile !== undefined){
                const formData = new FormData();
                let today = dayjs().format('YYYY-MM-DD');
                let count = 1;
                for (const file of data.image) {
                    const modifiedFile = new File([file], data.name + '__' + today.toString().replaceAll('-', '') + '_' + count+'.png', {
                        type: file.type,
                    });
                    count ++;
                    formData.append('images', modifiedFile);
                }
                console.log(data.strDate.replaceAll('-',''));
                formData.append('name', data.name);
                formData.append('birth', data.birth);
                formData.append('diagName', data.diagName);
                formData.append('email', data.email);
                formData.append('phone', data.phone);
                formData.append('bank', data.bank);
                formData.append('account', data.account.toString());
                formData.append('strDate', data.strDate.replaceAll('-',''));
                formData.append('signName', signFile);
                const res = await axios.post('/api/v1/dev/createPdf', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    }
                });
            }else {
                setIsOpen(true);
                setMsg(' .');
            }
        }catch (err){
            console.log(err);
        }
    }
    const onError = (errors: any) => {
        setIsOpen(true);
        setMsg('');
        if (errors.name) {
            setFocus('name');
            setMsg('  .');
        } else if (errors.birth) {
            setFocus('birth')
            setMsg('    .');
        } else if (errors.email) {
            setFocus('email')
            setMsg('  .');
        } else if (errors.phone) {
            setFocus('phone')
            setMsg('  .');
        }else if (errors.strDate) {
            setFocus('strDate')
            setMsg('   .');
        }else if (errors.diagName) {
            setFocus('diagName')
            setMsg('   .');
        }else if (errors.bank) {
            setFocus('bank')
            setMsg('  .');
        }else if (errors.account) {
            setFocus('account')
            setMsg('    .');
        }else if (errors.image) {
            setFocus('image')
            setMsg('   .');
        }
    };
    useEffect(() => {
        if (upload && upload.length > 0) {
            const file = upload[0];
            setImagePreview(URL.createObjectURL(file));
        }
    }, [upload,signFile]);
    return (
        <div className='pt-8  flex_center w-full flex-col pb-[90px]'>
            <div className='section flex-col'>
                <h2 className='title_text'>  </h2>
            </div>
            <form onSubmit={handleSubmit(onSubmit, onError)} encType="multipart/form-data">
                <div className='pt-8 w-full flex_center flex-col'>
                    <div className='flex flex-col'>
                        <label className='mr-auto text-[16px] font-bold pb-2'></label>
                        <input
                            placeholder={'  .'}
                            {...register('name', {
                                required: true,
                            })}
                            className="input"
                        />
                    </div>
                    <div className='flex flex-col mt-8'>
                        <label className='mr-auto text-[16px] font-bold pb-2'></label>
                        <input
                            placeholder={'-  .'}
                            {...register('birth', {
                                required: true,
                                pattern: {value: Regex.jumin, message: '    .'},
                            })}
                            maxLength={13}
                            className="input"
                        />
                    </div>
                    <div className='flex flex-col mt-8'>
                        <label className='mr-auto text-[16px] font-bold pb-2'></label>
                        <input
                            placeholder={'  .'}
                            {...register('email', {
                                required: true,
                            })}
                            className="input"
                        />
                    </div>
                    <div className='flex flex-col mt-8'>
                        <label className='mr-auto text-[16px] font-bold pb-2'></label>
                        <input
                            placeholder={'  .'}
                            {...register('phone', {
                                required: true,
                            })}
                            className="input"
                        />
                    </div>
                    <div className='flex flex-col mt-8 w-full justify-center'>
                        <label className='mr-auto text-[16px] font-bold pb-2'></label>
                        {/*<input*/}
                        {/*    placeholder={'  .'}*/}
                        {/*    {...register('temp2', {*/}
                        {/*        required: true,*/}
                        {/*    })}*/}
                        {/*    className="input"*/}
                        {/*/>*/}
                        <InputDatePicker control={control}/>
                    </div>
                    <div className='flex flex-col mt-8 w-full justify-center'>
                        <label className='mr-auto text-[16px] font-bold pb-2'></label>
                        <DiagSelect control={control}/>
                    </div>

                    <div className='flex flex-col mt-8 w-full justify-center'>
                        <div className='mr-auto text-[16px] font-bold pb-2'>
                              
                        </div>
                        <BankSelect control={control}/>
                    </div>
                    <div className='flex flex-col mt-8 w-full'>
                        <label className='mr-auto text-[16px] font-bold pb-2'>  </label>
                        <input
                            placeholder={'-  .'}
                            {...register('account', {
                                required: true,
                                pattern: {value: Regex.number, message: ' .'},
                            })}
                            className="input"
                        />
                        <span className='mt-2 text-red-600 font-normal mr-auto'>{errors?.account?.message}</span>
                    </div>
                    <div className='flex flex-col mt-8'>
                        <div className='mr-auto text-[16px] font-bold pb-2'>
                             
                        </div>
                        <div
                            className="w-[350px] h-40 rounded-lg border-2 border-dashed flex items-center justify-center">
                            <label htmlFor="picture" className="cursor-pointer text-center p-4 md:p-8">
                                {
                                    imagePreview ?
                                        <>
                                            <img src={imagePreview} alt={'uploadFile'} className='w-[280px] h-[140px]'/>
                                        </>
                                        :
                                        <>
                                            <svg className="w-10 h-10 mx-auto" viewBox="0 0 41 40" fill="none"
                                                 xmlns="http://www.w3.org/2000/svg">
                                                <path
                                                    d="M12.1667 26.6667C8.48477 26.6667 5.5 23.6819 5.5 20C5.5 16.8216 7.72428 14.1627 10.7012 13.4949C10.5695 12.9066 10.5 12.2947 10.5 11.6667C10.5 7.0643 14.231 3.33334 18.8333 3.33334C22.8655 3.33334 26.2288 6.19709 27.0003 10.0016C27.0556 10.0006 27.1111 10 27.1667 10C31.769 10 35.5 13.731 35.5 18.3333C35.5 22.3649 32.6371 25.7279 28.8333 26.5M25.5 21.6667L20.5 16.6667M20.5 16.6667L15.5 21.6667M20.5 16.6667L20.5 36.6667"
                                                    stroke="#4F46E5" strokeWidth="2" strokeLinecap="round"
                                                    strokeLinejoin="round"/>
                                            </svg>
                                            <p className="mt-3 text-gray-700 max-w-xs mx-auto">Click to <span
                                                className="font-medium text-indigo-600">Upload your  file</span> or drag
                                                and
                                                drop your file here</p>
                                        </>
                                }
                            </label>
                            <input {...register("image", {required: true})}
                                   id="picture"
                                   type="file"
                                   accept="image/*"
                                   className='hidden'
                                   multiple={true}
                            />
                        </div>
                    </div>
                    <Signature name={name} signature={signature} setSignature={setSignature} setSign={setSignFile}/>

                </div>
                <input type="submit" className='button_on mt-8'/>
            </form>
        </div>
    );
}

export default UserInfo;
